(function($){
    $(document).ready(function () {
        $('body').on('click', 'h3.ls-widget-heading', function () {
            var nxt = $(this).next();

            if (nxt.hasClass('group-open')) {
                nxt.removeClass('group-open');
                $(this).children('span').text('+');
            } else {
                nxt.addClass('group-open');
                $(this).children('span').text('-');
            }
        });

        // Add supports WP PageBuilder
        $(document).on('click', '.wppb-builder-edit-panel [data-ywc-widget-checkbox]', function () {
            $(this).attr('value', this.checked ? 1 : 0);
        });

        $(document).on('change', '.wppb-builder-edit-panel [data-ywc-widget-image]', function () {
            $(this).parent().find('img').attr('src', $(this).val());
        });

        $(document).on('click', '.wppb-builder-edit-panel [data-ywc-widget-multi-checkbox]', function () {
            var $that = $(this),
                checkboxName = $that.attr('name'),
                values = $("input[name='" + checkboxName + "']:checked").map(function (i, e) {
                    return e.value;
                }).toArray().join(',');

            $("input[name='" + checkboxName.replace('_item', '') + "']").attr('value', values);
        });
    });
})(jQuery);